.navbar {
  background-color: #5D4FE5;
  padding: 1rem 0;
  position: sticky;
  top: 0;
  z-index: 1000;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.navbar-brand {
  font-size: 1.5rem;
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
}

.navbar-links {
  display: flex;
  gap: 2rem;
}

.nav-link {
  color: rgba(255, 255, 255, 0.9);
  text-decoration: none;
  font-weight: 500;
  transition: color 0.2s ease-in-out;
}

.nav-link:hover {
  color: #ffffff;
}

.navbar-auth {
  display: flex;
  gap: 1rem;
  align-items: center;
}

.login {
  color: rgba(255, 255, 255, 0.9);
}

.signup {
  background-color: #ffffff;
  color: #5D4FE5;
  padding: 0.5rem 1.25rem;
  border-radius: 9999px;
  font-weight: 600;
}

.signup:hover {
  background-color: rgba(255, 255, 255, 0.9);
  color: #5D4FE5;
}

.user-menu {
  position: relative;
  padding: 0.5rem;
}

.user-name {
  color: #ffffff;
  font-weight: 500;
  cursor: pointer;
  display: flex;
  align-items: center;
  gap: 0.5rem;
}

.user-name:after {
  content: 'â–¼';
  font-size: 0.75rem;
}

.user-dropdown {
  position: absolute;
  top: 100%;
  right: 0;
  background-color: #ffffff;
  border-radius: 0.5rem;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  min-width: 200px;
  opacity: 0;
  visibility: hidden;
  transform: translateY(-10px);
  transition: all 0.2s ease-in-out;
}

.user-menu:hover .user-dropdown {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.dropdown-item {
  display: block;
  padding: 0.75rem 1rem;
  color: #1f2937;
  text-decoration: none;
  transition: background-color 0.2s ease-in-out;
  cursor: pointer;
  border: none;
  background: none;
  width: 100%;
  text-align: left;
  font-size: 1rem;
}

.dropdown-item:hover {
  background-color: #f3f4f6;
}

.dropdown-item:first-child {
  border-top-left-radius: 0.5rem;
  border-top-right-radius: 0.5rem;
}

.dropdown-item:last-child {
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
}

@media (max-width: 768px) {
  .navbar-links {
    display: none;
  }

  .navbar-auth {
    gap: 0.5rem;
  }

  .signup {
    padding: 0.5rem 1rem;
  }

  .user-menu {
    position: static;
  }

  .user-dropdown {
    position: fixed;
    top: 4rem;
    right: 1rem;
    left: 1rem;
  }
} 